#dark {
    --about-background: linear-gradient(to bottom, rgb(5, 5, 5), rgb(15, 25, 34), rgb(5, 5, 5));
    --about-font-color: #FFF;
    --about-card-background: #212121;
}

#light {
    --about-background: linear-gradient(to bottom, rgb(220, 220, 220), rgb(192, 236, 234), rgb(220, 220, 220));
    --about-font-color: #000;
    --about-card-background: #e2f2ff;
}

#about {
    width: 100%;
    min-height: 100vh;
    background: var(--about-background);
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--about-font-color);
}

#about h2 {
    margin: 3rem 0 1rem 0;
    font-size: 2.5rem;
    letter-spacing: 0.25rem;
}

#about p {
    max-width: 800px;
    text-align: center;
    padding: 2rem;
    font-size: 1.1rem;
    font-weight: 300;
    letter-spacing: 0.125rem;
}

#about .about-box {
    width: 100%;
    max-width: 1400px;
    padding: 1.2rem;
    margin-bottom: 2rem;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1rem;
}

#about .about-box .card {
    width: 350px;
    border-radius: 1rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--about-card-background);
    transition: .3s;
}

#about .about-box .card .material-symbols-outlined {
    font-size: 4rem;
    padding: 2rem;
    user-select: none;
}

#about .about-box .card h3 {
    margin: 0;
    font-size: 1.2rem;
    letter-spacing: 0.2rem;
}

#about .about-box .card ul {
    margin: 1.5rem;
    margin-left: 0;
    list-style-type: '>';
}

#about .about-box .card ul li {
    margin: 0.5rem;
    padding-left: 1rem;
}

#about .about-box .card .technologies {
    margin: 2rem 1rem;
    display: flex;
    flex-wrap: wrap;
    row-gap: 1rem;
    column-gap: 0.8rem;
}

#about .about-box .card .technologies .item {
    min-width: 45%;
    display: flex;
    align-items: center;
}

#about .about-box .card .list {
    margin: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

#about .about-box .card .list .item {
    display: flex;
    align-items: center;
}

#about .about-box .card .icon {
    width: 3rem;
    max-width: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

#about .about-box .card img {
    height: 1.3rem;
}

#about .about-box .card span {
    font-size: 1rem;
    white-space: nowrap;
}