#dark {
    --footer-font-color: #FFF;
    --footer-background: #ffffff0a;
}

#light {
    --footer-font-color: #000;
    --footer-background: #a5a5a5;
}

#footer {
    width: 100%;
    color: var(--footer-font-color);
    background: var(--footer-background);
    display: flex;
    align-items: center;
    justify-content: center;
}

#footer span {
    text-align: center;
    padding: 1rem;
    font-size: 0.8rem;
    font-weight: 300;
    letter-spacing: 0.14rem;
}