@import url("https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@48,400,0,0");

#dark {
  --modal-background: #212121;
  --modal-font-color: #FFFFFF;
  --modal-links-font-color: #FFF;
  --modal-links-background: rgba(0, 0, 0, 0.3);
  --modal-links-border-color: rgba(255, 255, 255, 0.3);
  --modal-links-hover-background: rgba(0, 0, 0, 0.8);
  --modal-links-hover-border-color: rgba(0, 0, 0, 0.8);
  --modal-description-font-color: #dddddd;
  --modal-technologies-background: rgba(0, 0, 0, 0.3);
}

#light {
  --modal-background: #bdbdbd;
  --modal-font-color: #000000;
  --modal-links-font-color: #000;
  --modal-links-background: rgba(255, 255, 255, 0.3);
  --modal-links-border-color: rgba(0, 0, 0, 0.3);
  --modal-links-hover-background: rgba(255, 255, 255, 0.8);
  --modal-links-hover-border-color: rgba(0, 0, 0, 0.8);
  --modal-description-font-color: #070707;
  --modal-technologies-background: rgba(255, 255, 255, 0.6);
}

.modal {
  width: 90%;
  max-width: 1000px;
  margin: 5%;
  background: var(--modal-background);
  border-radius: 20px;
  overflow-x: clip;
  display: flex;
  flex-direction: column;
  color: var(--modal-font-color);
}

.modal .gallery {
  position: relative;
  color: #FFF;
}

.modal .gallery img {
  width: 100%;
  height: 100%;
  max-height: 90vh;
  object-fit: cover;
  border-radius: 15px;
}

.modal .gallery div {
  cursor: pointer;
  height: 100%;
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  user-select: none;
}

.modal .gallery .prev {
  left: 0
}

.modal .gallery .next {
  right: 0;
}

.modal .gallery .prev:hover {
  background: linear-gradient(to right, #000, transparent);
}

.modal .gallery .next:hover {
  background: linear-gradient(to left, #000, transparent);
}

.modal .gallery .material-symbols-outlined {
  padding: 1.5rem;
  font-size: 2rem;
}

.modal .gallery .close {
  cursor: pointer;
  position: absolute;
  user-select: none;
  top: 0;
  right: 0;
  margin: 1rem;
  padding: 0.2rem;
  font-size: 1.5rem;
  background: radial-gradient(#000000b0, transparent);
  border: 2px solid transparent;
  border-radius: 50%;
  
}

.modal .gallery .close:hover {
  border: 2px solid #FFF;
  background-color: #000;
}

.modal .title-row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0.8rem 1.5rem;
  row-gap: 1.2rem;
  column-gap: 0.5rem;
}

.modal .title-row .title {
  min-width: 60%;
  flex-grow: 1;
  font-size: 1.5rem;
  letter-spacing: 0.05rem;
}

.modal .title-row .links {
  display: flex;
  align-self: flex-end;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  user-select: none;
}

.modal .title-row .links a {
  border-radius: 2rem;
  padding: 0.2rem 0.3rem 0.2rem 0.5rem;
  text-decoration: none;
  color: var(--modal-links-font-color);
  font-weight: 300;
  background: var(--modal-links-background);
  border: 2px solid var(--modal-links-border-color);
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.modal .title-row .links a.hide {
  display: none;
}

.modal .title-row .links a.block {
  cursor: not-allowed;
}

.modal .title-row .links a:hover {
  background: var(--modal-links-hover-background);
  border: 2px solid var(--modal-links-hover-border-color);
  transition: .3s;
}

.modal .title-row .links a span {
  white-space: nowrap;
  font-size: 0.9rem;
  letter-spacing: 0.02rem;
}

.modal .title-row .links a svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: var(--modal-links-font-color);
}

.modal .title-row .links a .material-symbols-outlined {
  font-size: 1.2rem;
  padding: 0.15rem;
}

.modal .description {
  padding: 0 1.5rem;
  text-align: justify;
  text-justify: inter-word;
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: 0.03rem;
  color: var(--modal-description-font-color);
}

.modal .description p {
  margin: 0.8rem 0;
}

.modal .group {
  padding-top: 1rem;
  font-size: 1.2rem;
  letter-spacing: 0.1rem;
}

.modal .features {
  margin: 1rem 0;
  padding: 0 1.5rem;
}

.modal .features ul {
  list-style: circle;
  padding: 0.5rem 1rem;
  margin: 0;
}

.modal .features ul li {
  padding: 0.25rem 0.2rem;
  font-weight: 300;
  letter-spacing: 0.05rem;
  font-size: 0.9rem;
}

.modal .technologies-row {
  margin: 0 1.5rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.modal .technologies-row .box {
  min-width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--modal-technologies-background);
  border-radius: 1rem;
}

.modal .technologies-row .list {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
}

.modal .technologies-row .item {
  display: flex;
  align-items: center;
}

.modal .technologies-row .item img {
  height: 1.3rem;
}

.modal .technologies-row .item span {
  padding: 0 0.5rem;
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: 0.05rem;
}