#dark {
    --logo-color: #FFF;
    --menu-item-font-color: #FFF;
    --menu-item-hover-font-color: #8ecccc;
    --menu-dropdown-background: #212121;
    --menu-switch-background: #ffffff33;
    --menu-switch-handle-color: #FFF;
}

#light {
    --logo-color: #000;
    --menu-item-font-color: #000;
    --menu-item-hover-font-color: #085f63;
    --menu-dropdown-background: rgb(165, 165, 165);
    --menu-switch-background: #00000066;
    --menu-switch-handle-color: #1f1f1f;
}

#header {
    width: 100%;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.header-box {
    width: 100%;
    min-width: 400px;
    max-width: 1400px;
    padding: 1rem 2rem;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header-box .logo {
    height: 50px;
    stroke: var(--logo-color);
    fill: var(--logo-color);
}

.header-box .menu {
    padding-left: 1rem;
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.header-box .menu a {
    color: var(--menu-item-font-color);
    text-decoration: none;
    letter-spacing: 0.1rem;
    font-weight: 500;
    transition: 0.3s;
    white-space: nowrap;
}

.header-box .menu a:hover {
    scale: 1.2;
    color: var(--menu-item-hover-font-color);
}

.header-box .menu a:active {
    scale: 0.6;
    color: var(--menu-item-hover-font-color);
}

.header-box .menu .settings {
    position: relative;
}

.header-box .menu .settings .material-symbols-outlined {
    cursor: pointer;
    max-width: 2rem;
    user-select: none;
    color: var(--menu-item-font-color);
    font-size: 1.5rem;
    padding: 0.5rem;
}

.header-box .menu .settings .dropdown-content {
    display: none;
    padding: 1rem 1rem 0 1rem;
    right: 0;
    position: absolute;
    background: var(--menu-dropdown-background);
    border-radius: 1rem 0 1rem 1rem;
    min-width: 160px;
    z-index: 2;
}

.header-box .menu .settings:hover .dropdown-content {
    display: block;
}

.header-box .menu .settings:hover .material-symbols-outlined {
    background: var(--menu-dropdown-background);
    border-radius: 1rem 1rem 0 0;
}

.header-box .menu .settings:hover .switch-row .material-symbols-outlined {
    background: none;
}

.header-box .menu .settings .dropdown-content .category {
    color: var(--menu-item-font-color);
    display: flex;
    justify-content: center;
    padding-bottom: 0.7rem;
    font-weight: 500;
    letter-spacing: 0.1rem;
}

.header-box .menu .settings .dropdown-content .switch-row {
    width: 100%;
    padding-bottom: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 1rem;
}

.dropdown-content .switch-row .switch {
    width: 2.4rem;
    height: 1.2rem;
    background: var(--menu-switch-background);
    display: flex;
    justify-content: flex-start;
    border-radius: 2rem;
    padding: 0.3rem;
    cursor: pointer;
}
  
.dropdown-content .switch-row .switch[data-ison='true'] {
    justify-content: flex-end;
}

.dropdown-content .switch-row .handle {
    width: 1.2rem;
    height: 1.2rem;
    background: var(--menu-switch-handle-color);
    border-radius: 50%;
}

.dropdown-content .switch-row .material-symbols-outlined {
    max-width: 2rem;
    font-size: 1.5rem;
}

.dropdown-content .switch-row .flag {
    width: 1.5rem;
    height: 1.5rem;
}

@media screen and (max-width: 650px) {
    .header-box {
        padding: 0.5rem 0.5rem;
    }

    .header-box .menu {
        gap: 1rem;
    }
}