#dark {
    --contact-background: linear-gradient(to bottom, rgb(5, 5, 5), rgb(15, 25, 34), rgb(5, 5, 5));
    --contact-font-color: #FFF;
    --contact-form-background: rgba(0, 0, 0, 0.4);
    --contact-form-input-background: #212121;
    --contact-form-button-background: #0a0a0a;
    --contact-form-button-hover-background: #1b1b1b;
    --contact-form-loader: #adadad;
}

#light {
    --contact-background: linear-gradient(to bottom, rgb(220, 220, 220), rgb(192, 236, 234), rgb(220, 220, 220));
    --contact-font-color: #000;
    --contact-form-background: rgba(105, 105, 105, 0.4);
    --contact-form-input-background: #ffffff;
    --contact-form-button-background: #085f63;
    --contact-form-button-hover-background: #0b878d;
    --contact-form-loader: #0b878d;
}

#contact {
    width: 100%;
    min-height: 100vh;
    background: var(--contact-background);
    color: var(--contact-font-color);
    display: flex;
    flex-direction: column;
    align-items: center;
}

#contact h2 {
    margin: 3rem;
    font-size: 2.5rem;
    letter-spacing: 0.25rem;
}

#contact form {
    width: 100%;
    max-width: 1000px;
    padding: 30px;
    box-sizing: border-box;
    background-color: var(--contact-form-background);
    border-radius: 1rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
}

#contact form input {
    min-width: 320px;
    font-size: 1rem;
    flex-grow: 1;
    padding: 0.8rem;
    letter-spacing: 0.05rem;
    box-sizing: border-box;
    border: none;
    outline: none;
    background: var(--contact-form-input-background);
    color: var(--contact-font-color);
    border-radius: 2rem;
}

#contact form textarea {
    width: 100%;
    height: 20vh;
    resize: none;
    font-size: 1rem;
    padding: 0.8rem;
    box-sizing: border-box;
    border: none;
    outline: none;
    border-radius: 2rem;
    background: var(--contact-form-input-background);
    color: var(--contact-font-color);
}

#contact form .error {
    margin: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    color: #FF0000;
    letter-spacing: 0.08rem;
    line-height: 1.5rem;
    white-space: pre-line;
    text-align: center;
}

#contact form .send-box {
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

#contact form button {
    width: 250px;
    user-select: none;
    font-size: 1rem;
    padding: 1rem;
    box-sizing: border-box;
    border-radius: 2rem;
    border: 1px solid var(--contact-form-input-background);
    letter-spacing: 0.05rem;
    font-weight: 500;
    letter-spacing: 0.1rem;
    cursor: pointer;
    background-color: var(--contact-form-button-background);
    transition: 0.2s;
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

#contact form button:hover {
    background-color: var(--contact-form-button-hover-background);
}

#contact form button.loading {
    display: none;
}

.info-box {
    position: fixed;
    top: 1rem;
    background-color: rgb(0, 150, 0);
    color: #FFF;
    padding: 1rem;
    border-radius: 1rem;
    letter-spacing: 0.05rem;
}

#contact .error-input {
    border: 2px solid #FF0000;
}

/* Loader from https://loading.io/css/ */
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 4rem;
  height: 4rem;
}

.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: var(--contact-form-loader);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}

.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}