#projects {
    width: 100%;
    min-height: 100vh;
    background-color: var(--projects-background-color);
    background-image: var(--projects-background-image);
    /* background by SVGBackgrounds.com */
    background-attachment: fixed;
    background-size: contain;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--projects-font-color);
}

#projects h2 {
    margin: 3rem 0 1rem 0;
    font-size: 2.5rem;
    letter-spacing: 0.25rem;
}

#projects .projects-box {
    width: 100%;
    max-width: 1400px;
    padding: 1.2rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}

#projects .projects-box .categories {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
}

#projects .projects-box .category {
    cursor: pointer;
    display: flex;
    align-items: center;
    background-color: var(--project-category-background);
    padding: 0.4rem 0.8rem;
    border-radius: 20px;
    gap: 0.6rem;
    user-select: none;
}

#projects .projects-box .category.selected {
    background-color: var(--project-category-background-selected); 
}

#projects .projects-box .category span {
    font-size: 1rem;
    letter-spacing: 0.05rem;
}

#projects .projects-box .category img {
    height: 1.5rem;
}

#projects .projects-box .project-list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

#projects .projects-box .project-list .project {
    width: 400px;
    max-width: 90%;
    height: 300px;
    border-radius: 1rem;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    user-select: none;
    border: 1px solid transparent;
    cursor: pointer;
    transition: .3s;
}

#projects .projects-box .project-list .project:hover {
    scale: 1.02;
    border: 1px solid var(--projects-font-color);
}

#projects .projects-box .project-list .project .preview {
    width: 100%;
    height: 60%;
    flex-grow: 1;
}

#projects .projects-box .project-list .project .preview img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#projects .projects-box .project-list .project .info {
    display: flex;
    flex-direction: column;
    background-color: var(--project-project-info-background);
}

#projects .projects-box .project-list .project .info .name {
    align-self: center;
    max-width: 90%;
    padding: 1rem;
    white-space: nowrap;
    overflow: hidden;
    font-size: 1.2rem;
    letter-spacing: 0.05rem;
}

#projects .projects-box .project-list .project .info .technologies {
    max-width: 90%;
    align-self: flex-end;
    padding: 0.5rem;
    padding-top: 0.1rem;
    display: flex;
    gap: 0.2rem;
    overflow: hidden;
}

#projects .projects-box .project-list .project .info .technologies div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--project-project-technology-background);
    border-radius: 50%;
}

#projects .projects-box .project-list .project .info .technologies img {
    height: 1.2rem;
    padding: 0.35rem;
}

#projects .project-list .no-data {
    padding: 2rem;
    font-size: 1.2rem;
    letter-spacing: 0.1rem;
    display: none;
}

#projects .project-list .no-data:first-child {
    display: block;
}